import React, {useState,useEffect} from "react";
import queryString from 'query-string';
import io from 'socket.io-client';
import classes from './index.module.css'
import InfoBar from "../InforBar/InfoBar";
import Input from '../Input/Input'
import Messages from '../messages/messages'
import UsersInRoom from "../UsersinRoom/UsersinRoom";

let socket;

const Chat = (props)=>{
    const [name,setName] = useState('');
    const [room,setRoom] = useState('');
    const [message,setMessage] = useState('');
    const [messages,setMessages] =useState([])
    const [users,setUsers] = useState([]);
    const ENDPOINT = "https://chattoroom.herokuapp.com"

    useEffect(()=>{

        const { name, room } = queryString.parse(props.location.search);
        socket = io(ENDPOINT);
        setName(name);
        setRoom(room);
        socket.emit('join',{name,room}, (error)=>{alert(error); props.history.goBack()});
        return ()=>{
            socket.emit('disconnect');
            socket.off();
        }

    },[ENDPOINT, props.location.search])

    useEffect(()=>{
        socket.on('message',(message)=>{
            setMessages([...messages,message]);

        }, [messages])

        socket.on("roomData", ({ users }) => {
            setUsers(users);
        });
    })

    const sendMessage= (event)=>{
        event.preventDefault();

        if(message){
            socket.emit('sendMessage',message,()=>setMessage(''))
        }
    }

    return (
        <div className={classes.outerContainer}>
            <div className={classes.container}>
                <InfoBar room={room}/>
                <Input message={message} setMessage={setMessage} sendMessage={sendMessage}/>
                <Messages messages={messages} name={name}/>
                <UsersInRoom users={users}/>
            </div>
        </div>
    )
}

export default Chat;